@charset "UTF-8";
/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
.text---primary {
  color: #6139cf !important;
}

.bg---primary {
  background-color: #6139cf !important;
}

.text---secondary {
  color: #007335 !important;
}

.bg---secondary {
  background-color: #007335 !important;
}

.text---white {
  color: #fff !important;
}

.bg---white {
  background-color: #fff !important;
}

.text---black {
  color: #1b1b1b !important;
}

.bg---black {
  background-color: #1b1b1b !important;
}

* {
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: calc(1vw + 1.5rem);
}

h2 {
  font-size: calc(1vw + 1.3rem);
}

h3 {
  font-size: calc(1vw + 1.2rem);
}

p {
  font-size: calc(1vw + 0.2rem);
}

@media (max-width: 1399.98px) {
  p {
    font-size: calc(1vw + 0.4rem);
  }
}
@media (max-width: 1199.98px) {
  p {
    font-size: calc(1vw + 0.6rem);
  }
}
@media (max-width: 991.98px) {
  p {
    font-size: calc(1vw + 0.8rem);
  }
}
@media (max-width: 767.98px) {
  p {
    font-size: calc(1vw + 0.9rem);
  }
}
@media (max-width: 575.98px) {
  p {
    font-size: calc(1vw + 1rem);
  }

  h1,
h2,
h3,
h4,
p {
    text-align: center;
  }
}
/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
/**
